<template>
  <div class="row q-col-gutter-md q-pb-md">
    <div class="col-12 col-sm-6" v-for="opt in selection" :key="opt">
      <q-checkbox
        autofocus
        v-model="modelValue"
        :val="opt"
        :label="opt.label"
        :name="name || undefined"
        :color="opt.color || 'secondary'"
        class="field-select text-body1 font-medium rounded bg-none text-primary-500 q-py-sm q-pl-sm full-width flex-label"
        :rules="[(val) => (required ? !!val || '' : true)]"
      />
    </div>
  </div>
</template>

<script>
import { defineComponent, ref } from 'vue';

export default defineComponent({
  name: 'CheckboxSelect',

  emits: ['updated'],

  props: {
    initModel: {},
    selection: {
      type: Array,
      default: () => [],
    },
    name: {
      type: String,
      default: '',
    },
    required: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      modelValue: ref([]),
    };
  },

  watch: {
    initModel: {
      async handler(val) {
        if (val) this.modelValue = val;
      },
    },
    modelValue: {
      async handler(val) {
        this.$emit('updated', val);
      },
    },
  },

  mounted() {
    if (this.initModel) this.modelValue = this.initModel;
  },
});
</script>
